import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

var routes = []

const Login = () => import('@/components/Login')
const Dashboard = () => import('@/components/Dashboard')

const Overviewheader = () => import('@/components/Overview-header')
const Overviewmain = () => import('@/components/Overview-main')

const Detailheader = () => import('@/components/Detail-header')
const Detailmain = () => import('@/components/Detail-main')
const Detailtrends = () => import('@/components/Detail-trends')
const Detailentries = () => import('@/components/Detail-entries')
const Detailsuggestions = () => import('@/components/Detail-suggestions')
const Detailalerts = () => import('@/components/Detail-alerts')

routes.push({
  path: '/',
  redirect: '/login'
})

routes.push({
  path: '/login',
  name: '/login',
  meta: {
    requireLogin: true
  },
  component: Login
})

routes.push({
	path: '/Dashboard',
	name: '/Dashboard',
  meta: {
    requireAuth: true,
  },
	components: {
    default: Dashboard,
	},
  children: [
      {
        path: 'Overview',
        components: {
          header: Overviewheader,
          main: Overviewmain
        }
      },
      {
        path: 'Detail',
        components: {
          header: Detailheader,
          main: Detailmain
        },
        children: [
        {
          path: 'trends',
          component: Detailtrends
        },
        {
          path: 'entries',
          component: Detailentries
        },
        {
          path: 'suggestions',
          component: Detailsuggestions
        },
        {
          path: 'alerts',
          component: Detailalerts
        }
        ]
      }
    ]
})

export default new Router({ routes })


