import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
let store = new Vuex.Store({
    // 1. state
    state:{
        category: [],
        user: window.sessionStorage.getItem('user'),
        token: window.sessionStorage.getItem('token'),
        patient_id: window.sessionStorage.getItem('patient_id'),
        patient_name: window.sessionStorage.getItem('patient_name')
    },

    // // 2. getters
    getters:{
        // 参数列表state指的是state数据
        getCategoryFn(state){
            return state.category;
        },
        getUserFn(state){
            return state.user;
        },
        getTokenFn(state){
            return state.token;
        },
        getPatient_idFn(state){
          return state.patient_id;
        },
        getPatient_nameFn(state){
          return state.patient_name;
        }
    },
    // 3. actions
    // 通常跟api接口打交道
    actions:{
        setCategoryData({commit,state}, raw){
            commit("setCategory", raw);
        },
        setUserData({commit,stat}, raw){
            commit("setUser", raw)
        },
        setTokenData({commit,stat}, raw){
            commit("setToken", raw)
        },
        setPatient_idData({commit,stat}, raw){
          commit("setPatient_id", raw)
        },
        setPatient_nameData({commit,stat}, raw){
          commit("setPatient_name", raw)
        }
    },
    // 4. mutations
    mutations:{
        // state指的是state的数据
        // axios
        setCategory(state, raw){
            state.category = raw;
        },
        setUser(state, raw){
            state.user = raw;
            window.sessionStorage.setItem('user', raw);
        },
        setToken(state, raw){
            state.token = raw;
            window.sessionStorage.setItem('token', raw);
        },
        setPatient_id(state, raw){
          state.patient_id = raw;
          window.sessionStorage.setItem('patient_id', raw);
        },
        setPatient_name(state, raw){
          state.patient_name = raw;
          window.sessionStorage.setItem('patient_name', raw);
        }

    }
});

export default store;


